import React, { useEffect, useState, useContext } from "react";
import { useHistory } from "react-router-dom";
import { Container, Input, Avatar } from "@material-ui/core";
import MaterialTable from "material-table";
//import { db_firestore } from "../config/firebase";
import { makeStyles } from "@material-ui/core/styles";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import axios from "axios";
import { getSellers } from "../middleware/Middleware";
import { AuthContext } from "../contexts/AuthContext";
import Chip from "@material-ui/core/Chip";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";

const Sellers = (props) => {
  const [sellers, setSellers] = useState([]);
  const [open, setOpen] = useState(false);
  const apiUrl = process.env.REACT_APP_URL_API;
  const history = useHistory();

  const {
    user: { user },
  } = useContext(AuthContext);

  const get = async () => {
    const sels = await getSellers(user.idcompany);
    console.log("Sellers ", sels);
    setSellers(sels);
  };

  /*eslint-disable */
  useEffect(() => {
    get();
  }, []);
  /*eslint-enable */

  const useStyles = makeStyles((theme) => ({
    sellersTable: {
      marginTop: "40px",
    },
    popList: {
      width: "100%",
    },
    uploadFile: {
      borderRadius: "30%",
      cursor: "pointer",
      "&> img": {
        width: "100%",
        objectFit: "cover",
      },
    },
  }));
  const classes = useStyles();

  const [file, setFile] = useState();

  const uploadImage = (e) => {
    const file = e.target.files[0];
    if (file.type.indexOf("image") < 0) {
      setFile();
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onloadend = function (e) {
      setFile(reader.result);
    };
  };

  const goToVendedor = (seller) => {
    history.push({
      pathname: "/",
      id: seller.uid,
      to2: "seller",
    });
  };

  //SnackBar
  function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
  }

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  return (
    <Container className={classes.sellersTable}>
      <MaterialTable
        title="Sellers"
        columns={[
          {
            title: "Avatar",
            field: "avatar",
            editable: "always",
            editComponent: (props) => (
              <div>
                <Avatar className={classes.uploadFile}>
                  <img
                    src={file ? file : "./imgs/fileUpload.png"}
                    alt="avatar"
                    onClick={() =>
                      document.getElementById("fileUpload").click()
                    }
                  />
                </Avatar>
                <Input
                  type="file"
                  id="fileUpload"
                  name="file"
                  style={{
                    display: "none",
                  }}
                  inputProps={{ accept: "image/*" }}
                  //onChange={(e) => props.onChange(e.target.value)}
                  onChange={(e) => uploadImage(e)}
                />
              </div>
            ),
            render: (rowData) =>
              rowData.avatar ? (
                <img
                  src={rowData.avatar}
                  alt="avatar"
                  style={{ width: 50, borderRadius: "30%" }}
                />
              ) : (
                <Avatar>
                  {rowData.name
                    .match(/\b(\w)/g)
                    .join("")
                    .toUpperCase()}
                </Avatar>
              ),
            headerStyle: {
              width: "25%",
            },
          },
          {
            title: "Email",
            field: "mail",
            editable: "always",
            headerStyle: {
              width: "30%",
            },
          },
          {
            title: "Password",
            field: "password",
            editable: "always",
            headerStyle: {
              width: "30%",
              textAlign: "center",
            },
          },
          {
            title: "Name",
            field: "name",
            editable: "always",
            headerStyle: {
              width: "30%",
            },
          },
          {
            title: "NPS",
            field: "nps",
            editable: "never",
            headerStyle: {
              width: "30%",
            },
          },
          {
            title: "Campaas asignadas",
            field: "campaas",
            editable: "never",
            render: (rowData) => (
              <div>
                {rowData && rowData !== undefined
                  ? rowData.campaigns.map((camp) => (
                      <Chip
                        style={{ marginRight: "5px", marginBottom: "5px" }}
                        label={camp.name}
                        variant="default"
                        size="small"
                        color="primary"
                      />

                      // <span
                      //   style={{
                      //     margin: "0 5px",
                      //     fontSize: "0.875rem",
                      //     fontFamily:
                      //       '"Roboto", "Helvetica", "Arial", sans-serif',
                      //     fontWeight: "400",
                      //   }}
                      //   key={camp.name}
                      // >
                      //   <strong style={{ marginRight: "5px" }}>|</strong>
                      //   {camp.name}
                      // </span>
                    ))
                  : null}
              </div>
            ),
            headerStyle: {
              width: "50%",
              textAlign: "center",
            },
          },
        ]}
        data={sellers}
        editable={{
          onRowAdd: (newData) =>
            new Promise((resolve, reject) => {
              axios({
                method: "post",
                url: `${apiUrl}users`,
                headers: {
                  "Content-Type": "application/json",
                  Accept: "application/json",
                },
                data: {
                  avatar: newData.avatar,
                  mail: newData.mail,
                  password: newData.password,
                  name: newData.name,
                  displayName: newData.name,
                  role: "seller",
                  admin: false,
                  idcompany: user.idcompany,
                },
              })
                .then((resp) => {
                  setOpen(true);
                  resolve();
                  goToVendedor(resp.data.userRecord);
                })
                .catch((error) => {
                  console.log("error adding seller", error);
                  //setSellers([]);
                  reject();
                });
            }),
          onRowUpdate: (newData, oldData) =>
            new Promise((resolve, reject) => {
              axios({
                method: "post",
                url: `${apiUrl}users/edit`,
                headers: {
                  "Content-Type": "application/json",
                  Accept: "application/json",
                },
                data: {
                  mail: newData.mail,
                  //phoneNumber: newData.phone || null,
                  displayName: newData.name,
                  photoURL: newData.avatar,
                  uid: newData.uid,
                  name: newData.name,
                  nps: newData.nps || 0,
                  admin: false,
                  role: "seller",
                },
              })
                .then((res) => {
                  if (
                    newData.password &&
                    newData.password !== undefined &&
                    newData.password !== ""
                  )
                    axios({
                      method: "post",
                      url: `${apiUrl}users/update-password`,
                      headers: {
                        "Content-Type": "application/json",
                        Accept: "application/json",
                      },
                      data: {
                        password: newData.password,
                        uid: newData.uid,
                      },
                    })
                      .then((res) => {
                        setOpen(true);
                        resolve();
                        goToVendedor(newData);
                      })
                      .catch((err) => console.log(err));
                  else {
                    setOpen(true);
                    resolve();
                    goToVendedor(newData);
                  }
                })
                .catch((err) => {
                  console.log(err);
                  reject();
                });
            }),
          onRowDelete: (oldData) =>
            axios
              .delete(`${apiUrl}users/${oldData.uid}`)
              .then((res) => {
                setOpen(true);
                setSellers(sellers.filter((s) => s.uid !== oldData.uid));
              })
              .catch((err) => console.log(err)),
        }}
        actions={[
          {
            icon: ArrowForwardIosIcon,
            tooltip: "Ver Vendedor",
            onClick: (event, rowData) => goToVendedor(rowData),
          },
        ]}
        options={{
          pageSize: 10,
          pageSizeOptions: [5, 10, 20],
        }}
      />
      <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="success">
          Se guardaron los cambios
        </Alert>
      </Snackbar>
    </Container>
  );
};

export default Sellers;
