import React, { useState, useEffect, useContext } from "react";
import {
  Container,
  MenuItem,
  FormControl,
  Select,
  Avatar,
  Input,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import MaterialTable from "material-table";
import axios from "axios";
import { db_firestore } from "../config/firebase";
import {
  getAdmins,
  getCampaign,
  getCampaignsForCompany,
} from "../middleware/Middleware";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";
import { AuthContext } from "../contexts/AuthContext";

const Admins = () => {
  const apiUrl = process.env.REACT_APP_URL_API;
  const [users, setUsers] = useState();
  const [campaigns, setCampaigns] = useState();
  const [open, setOpen] = useState(false);
  const {
    user: { user },
  } = useContext(AuthContext);

  const get = async () => {
    const data = await getAdmins(user.idcompany);
    console.log("users -!->", data);
    setUsers(data);
  };

  useEffect(() => {
    if (!users) {
      get();
    }

    const getCamps = async () => {
      const camps = await getCampaignsForCompany(user.idcompany);
      let infoCamp = [];
      camps.forEach(async (camp) => {
        let campaign = await getCampaign(camp);
        infoCamp.push(campaign);
      });
      console.log("camps", infoCamp);
      setCampaigns(infoCamp);
    };
    getCamps();
  }, [users]);

  const [file, setFile] = useState();

  const uploadImage = (e) => {
    const file = e.target.files[0];
    if (file.type.indexOf("image") < 0) {
      setFile();
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onloadend = function (e) {
      setFile(reader.result);
    };
  };

  const useStyles = makeStyles((theme) => ({
    sellersTable: {
      marginTop: "40px",
    },
    popList: {
      width: "100%",
    },
    uploadFile: {
      borderRadius: "30%",
      cursor: "pointer",
      "&> img": {
        width: "100%",
        objectFit: "cover",
      },
    },
  }));
  const classes = useStyles();

  //SnackBar
  function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
  }

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  if (!users || !campaigns) return <div>Loading</div>;
  return (
    <Container>
      <MaterialTable
        title="Admins"
        style={{ marginBottom: "50px", width: "102%" }}
        columns={[
          {
            title: "Avatar",
            field: "avatar",
            editable: "always",
            render: (rowData) =>
              rowData.avatar ? (
                <img
                  src={rowData.avatar}
                  alt="avatar"
                  style={{ width: 50, borderRadius: "30%" }}
                />
              ) : (
                <Avatar>
                  {rowData.name
                    .match(/\b(\w)/g)
                    .join("")
                    .toUpperCase()}
                </Avatar>
              ),
            editComponent: (props) => (
              <div>
                <Avatar className={classes.uploadFile}>
                  <img
                    src={file ? file : "./imgs/fileUpload.png"}
                    alt="avatar"
                    onClick={() =>
                      document.getElementById("fileUpload").click()
                    }
                  />
                </Avatar>
                <Input
                  type="file"
                  id="fileUpload"
                  name="file"
                  style={{
                    display: "none",
                  }}
                  inputProps={{ accept: "image/*" }}
                  onChange={(e) => uploadImage(e)}
                />
              </div>
            ),
            headerStyle: {
              width: "25%",
            },
          },
          {
            title: "Email",
            field: "mail",
            editable: "onAdd",
            headerStyle: {
              width: "30%",
            },
          },
          {
            title: "Name",
            field: "name",
            editable: "always",
            headerStyle: {
              width: "30%",
            },
            cellStyle: {
              padding: "0px",
            },
          },
          {
            title: "Password",
            field: "password",
            editable: "onAdd",
            headerStyle: {
              width: "30%",
            },
          },
          {
            title: "Campaign",
            field: "idCampaign",
            editable: "always",
            headerStyle: {
              width: "30%",
              textAlign: "center",
            },
            render: (rowData) => {
              var aux = campaigns.filter(
                (camp) => camp.id === rowData.idCampaign
              );
              return <div>{aux.length > 0 ? aux[0].name : "Not Found"}</div>;
            },
            editComponent: (props) => (
              <FormControl>
                <Select
                  labelId="campaign-select"
                  label="Campaing"
                  id="campaign-select"
                  value={
                    props.rowData.idCampaign
                      ? props.rowData.idCampaign
                      : campaigns[0].id
                  }
                  onChange={(e) => props.onChange(e.target.value)}
                >
                  {campaigns.map((camp) => (
                    <MenuItem key={camp.id} value={camp.id}>
                      {camp.name}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            ),
          },
        ]}
        data={users}
        editable={{
          onRowAdd: (newData) =>
            new Promise((resolve, reject) => {
              axios({
                method: "post",
                url: `${apiUrl}users`,
                headers: {
                  "Content-Type": "application/json",
                  Accept: "application/json",
                },
                data: {
                  mail: newData.mail,
                  avatar: file || null,
                  password: newData.password,
                  name: newData.name,
                  displayName: newData.name,
                  idcompany: user.idcompany,
                  role: "client",
                  admin: true,
                },
              })
                .then((resp) => {
                  setOpen(true);
                  get();
                  resolve();
                })
                .catch((error) => {
                  console.log(error);
                  reject();
                });
            }),
          onRowUpdate: (newData, oldData) =>
            new Promise((resolve, reject) => {
              axios({
                method: "post",
                url: `${apiUrl}users/edit`,
                headers: {
                  "Content-Type": "application/json",
                  Accept: "application/json",
                },
                data: {
                  mail: newData.mail,
                  phoneNumber: newData.phone || null,
                  displayName: newData.name,
                  photoURL: newData.avatar || null,
                  uid: newData.uid,
                  name: newData.name,
                  admin: true,
                  nps: newData.nps || 0,
                  role: newData.role || "client",
                },
              })
                .then((res) => {
                  let listen = db_firestore
                    .collection("users")
                    .where("role", "==", "client")
                    .onSnapshot((docs) => {
                      setOpen(true);
                      get();
                      listen();
                    });

                  resolve();
                })
                .catch((err) => {
                  console.log(err);
                  reject();
                });
            }),
          onRowDelete: (oldData) =>
            axios
              .delete(`${apiUrl}users/${oldData.uid}`)
              .then((res) => {
                setOpen(true);
                setUsers(users.filter((u) => u.uid !== oldData.uid));
              })
              .catch((err) => console.log(err)),
        }}
        options={{
          pageSize: 10,
          pageSizeOptions: [5, 10, 20],
        }}
      />
      <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="success">
          Se guardaron los cambios
        </Alert>
      </Snackbar>
    </Container>
  );
};

export default Admins;
