import React, { useContext, useState, useEffect } from "react";
import { AuthContext } from "../contexts/AuthContext";
import CircularProgress from "@material-ui/core/CircularProgress";
import {
  getCampaigns,
  getCompany,
  getSellers,
  getChats,
  getSessions,
} from "../middleware/Middleware";
import { Container, Paper, Button } from "@material-ui/core";
import { useHistory } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import NpsReport from "./NpsReport";
import StatsReport from "./StatsReport";

const Reports = () => {
  const history = useHistory();
  const {
    user: { user },
  } = useContext(AuthContext);
  const [company, setCompany] = useState();
  const [camps, setCamps] = useState([]);
  const [loading, setLoading] = useState(true);
  const [bestSeller, setBestSeller] = useState();
  const [bestCampaign, setBestCampaign] = useState();

  const [sellers, setSellers] = useState([]);
  const [avrgQueryTime, setAvrgQueryTime] = useState(0);
  const [adherence, setAdherence] = useState(0);
  const [effectiveness, setEffectiveness] = useState(0);
  const [attention1, setAttention1] = useState(0);
  const [attention2, setAttention2] = useState(0);
  const [dropOuts1, setDropouts1] = useState(0);
  const [dropOuts2, setDropouts2] = useState(0);
  const [entries, setEntries] = useState(0);

  useEffect(() => {
    const getSellersCampaign = (id, allSellers) => {
      var res = [];

      for (const key in allSellers) {
        if (allSellers[key].hasOwnProperty("campaigns")) {
          const campaigns = allSellers[key].campaigns;
          if (campaigns.some((c) => c.id === id)) {
            const selCamp = campaigns.filter((c2) => c2.id === id);
            res.push({
              ...allSellers[key],
              nps: selCamp[0].nps ? selCamp[0].nps : 0,
              bestSeller: false,
            });
          }
        }
      }
      return res;
    };

    const get = async () => {
      var camps = await getCampaigns(user.idcompany);
      //console.log("camps", camps);
      const company1 = await getCompany(user.idcompany);

      var sellersAll = await getSellers(user.idcompany);
      sellersAll = sellersAll.filter((s) => s.hasOwnProperty("mail"));

      //Stats for sellers
      var queryTime = 0;
      var countChats = 0;
      var countAttended = 0;
      var countShortAttended = 0;
      var countRejected = 0;
      var countTotalUsers = 0;
      for (let i = 0; i < sellersAll.length; i++) {
        const sel = sellersAll[i];

        const chats = await getChats(sel.uid);
        const sessions = await getSessions(sel.uid);
        //const services = await getServices(sel.uid);

        sel["chats"] = chats;
        sel["sessions"] = sessions;
        //sel["services"] = services;
        var selQueryTime = 0;
        if (chats.length > 0) {
          //console.log("chats", chats);
          //console.log("sessions", sessions);
          //console.log("services", services);
          countChats += chats.length;
          for (let c = 0; c < chats.length; c++) {
            const chat = chats[c];
            selQueryTime += chat.tiempoTotal;
          }
          queryTime += selQueryTime;
        }
        var selAttended = 0;
        var selShortAttended = 0;
        var selRejected = 0;
        var selTotalUsers = 0;
        if (sessions.length > 0)
          for (let s = 0; s < sessions.length; s++) {
            const session = sessions[s];
            if (
              session.hasOwnProperty("atendidos") &&
              session.hasOwnProperty("rechazados")
            ) {
              selAttended += Number(session.atendidos);
              selShortAttended += Number(session.antecionesCortas);
              selRejected += Number(session.rechazados);
              selTotalUsers += Number(session.totalUsuarios);
            }
          }

        sel["avrgQueryTime"] =
          selQueryTime > 0 ? selQueryTime / chats.length : 0;
        sel["adherence"] = 0;
        sel["effectiviness"] =
          selRejected > 0
            ? Math.round((selAttended / selTotalUsers) * 100) / 100
            : 0;
        sel["attention2"] =
          selAttended > 0
            ? Math.round((selAttended / (selTotalUsers - selRejected)) * 100) /
            100
            : 0;
        sel["dropouts1"] =
          selRejected > 0
            ? Math.round((selRejected / selAttended) * 100) / 100
            : 0;
        sel["droputs2"] =
          selRejected > 0
            ? Math.round(
              ((selRejected - selShortAttended) /
                (selAttended - selShortAttended)) *
              100
            ) / 100
            : 0;
        sel["entries"] = chats.length;
        sel["service1"] = 0;
        sel["service2"] = 0;
        sel["service3"] = 0;

        countAttended += selAttended;
        countShortAttended += selShortAttended;
        countRejected += selRejected;
        countTotalUsers += selTotalUsers;
      }
      setSellers(sellersAll);

      let vAvrgQueryTime = queryTime / countChats;
      if (isNaN(vAvrgQueryTime))
        vAvrgQueryTime = 0;
      setAvrgQueryTime(vAvrgQueryTime);

      let vAttention1 = Math.round((countAttended / countTotalUsers) * 100) / 100;
      if (isNaN(vAttention1))
        vAttention1 = 0;
      setAttention1(vAttention1);

      let vAttention2 = Math.round((countAttended / (countTotalUsers - countRejected)) * 100) / 100;
      if (isNaN(vAttention2))
        vAttention2 = 0;
      setAttention2(vAttention2);

      let vDropouts1 = Math.round((countRejected / countAttended) * 100) / 100;
      if (isNaN(vDropouts1))
        vDropouts1 = 0;
      setDropouts1(vDropouts1);

      let vDropouts2 = Math.round(
        ((countRejected - countShortAttended) /
          (countAttended - countShortAttended)) *
        100
      ) / 100;

      if (isNaN(vDropouts2))
        vDropouts2 = 0;

      setDropouts2(vDropouts2);

      setEntries(countChats);
      //Stats for sellers

      //console.log("sellersAll", sellersAll);
      /*for (let i = 0; i < camps.length; i++) {
        const camp = camps[i];
        let sellers = [];
        sellers = getSellersCampaign(camp.id, sellersAll);
        //console.log("sellers " + camp.name, sellers);
        camp["sellers"] = sellers;
      }*/

      camps = camps.map((c) => ({
        ...c,
        sellers: getSellersCampaign(c.id, sellersAll),
      }));

      var npsTotal = 0;
      let maxNpsCampaign = -1;
      for (let i = 0; i < camps.length; i++) {
        const camp = camps[i];
        let npsCamp = 0;

        let maxNps = 0;
        let indexSel = -1;
        for (let j = 0; j < camp.sellers.length; j++) {
          const sel = camp.sellers[j];
          if (sel.nps !== NaN) npsCamp += Number(sel.nps);

          if (sel.nps !== NaN && Number(sel.nps) > maxNps) {
            indexSel = j;
            maxNps = Number(sel.nps);
          }
        }

        if (indexSel > -1) {
          camps[i]["sellers"][indexSel]["bestSeller"] = true;
          setBestSeller(camps[i]["sellers"][indexSel]);
        }

        camps[i]["nps"] =
          camps[i]["sellers"].length > 0
            ? npsCamp / camps[i]["sellers"].length
            : 0;
        if (camps[i]["nps"] > maxNpsCampaign) {
          maxNpsCampaign = camps[i]["nps"];
          setBestCampaign(camps[i]);
        }
        npsTotal +=
          camps[i]["sellers"].length > 0
            ? npsCamp / camps[i]["sellers"].length
            : 0;
      }
      setCamps(camps);
      company1["nps"] = Math.round((npsTotal / camps.length) * 100) / 100;
      setCompany(company1);
      setLoading(false);
    };
    if (camps && camps.length === 0) {
      get();
    }
  }, [camps]);

  const goToReport = (name) => {
    history.push({
      pathname: "/",
      search: "",
      hash: "",
      state: { to: name, company, camps },
    });
  };

  const useStyles = makeStyles((theme) => ({
    reportsCointainer: {
      paddingBottom: "40px",
    },
    reportsList: {
      width: "100%",
      marginBottom: "20px",
      borderRadius: "0px 50px 0px 0px",
    },
    report: {
      paddingLeft: "10px",
      paddingRight: "10px",
    },
    headerDiv: {
      backgroundColor: "#241332",
      borderRadius: "0px 50px 0px 50px",
      color: "white",
      textAlign: "center",
      height: "60px",
      fontSize: "40px",
      letterSpacing: "4px",
      marginBottom: "20px",
    },
    detailButton: {
      width: "100%",
      marginTop: "20px",
    },
  }));
  const classes = useStyles();

  if (loading) return <CircularProgress />;
  console.log("camps", camps);
  return (
    <Container className={classes.reportsCointainer}>
      <Paper className={classes.reportsList} elevation={3}>
        <div className={classes.headerDiv}>
          <p> NPS</p>
        </div>
        <div className={classes.report}>
          <NpsReport
            company={company}
            camps={camps}
            loading={loading}
            bestSeller={bestSeller}
            bestCampaign={bestCampaign}
          />
        </div>
        <Paper elevation={3}>
          <Button
            className={classes.detailButton}
            size="large"
            variant="outlined"
            color="primary"
            onClick={() => goToReport("reports/NpsReportDetails")}
          >
            Details
          </Button>
        </Paper>
      </Paper>
      <Paper className={classes.reportsList} elevation={3}>
        <div className={classes.headerDiv}>
          <p> STATISTICS</p>
        </div>
        <div className={classes.report}>
          <StatsReport
            avrgQueryTime={avrgQueryTime}
            adherence={adherence}
            effectiveness={effectiveness}
            attention1={attention1}
            attention2={attention2}
            dropOuts1={dropOuts1}
            dropOuts2={dropOuts2}
            entries={entries}
          />
        </div>
        <Paper elevation={3}>
          <Button
            className={classes.detailButton}
            size="large"
            variant="outlined"
            color="primary"
            onClick={() => goToReport("reports/StatsReportDetails")}
          >
            Details
          </Button>
        </Paper>
      </Paper>
    </Container>
  );
};

export default Reports;

