import React, { useState, useEffect, useContext } from "react";
import { Container, Paper, Grid } from "@material-ui/core";
import { Link, useHistory } from "react-router-dom";
import PersonIcon from "@material-ui/icons/Person";
import StarIcon from "@material-ui/icons/Star";
import EqualizerIcon from "@material-ui/icons/Equalizer";
import VisibilityIcon from "@material-ui/icons/Visibility";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import Avatar from "@material-ui/core/Avatar";
import { makeStyles } from "@material-ui/core/styles";
import MaterialTable from "material-table";
import {
  getOnlineSellers,
  getOfflineSellers,
  getSellers,
  getCampaignsForCompany,
} from "../middleware/Middleware";
import { db_realtime } from "../config/firebase";
import { AuthContext } from "../contexts/AuthContext";

const Main = (props) => {
  const [sellers, setSellers] = useState([]);
  const [camps, setCamps] = useState([]);
  const [offLineSellers, setOfflineSeller] = useState([]);
  const [totalNps, setTotalNps] = useState(0);
  const [lastUpdate, setNewUpdate] = useState();
  const history = useHistory();

  const getOnline = async (sellers) => {
    const sellersOnline = await getOnlineSellers(sellers, user.idcompany);
    let rightNow = new Date();
    rightNow = rightNow.getHours() + ":" + rightNow.getMinutes();

    setSellers(sellersOnline);
    setNewUpdate(rightNow);
    callGetSellers();
  };
  const getOffline = async () => {
    const sellersOffline = await getOfflineSellers(user.idcompany);
    setOfflineSeller(sellersOffline);
  };

  const callGetSellers = async () => {
    let sellers = await getSellers(user.idcompany),
      avgNPS = 0,
      cant = 0;
    if (sellers.length > 0) {
      sellers.forEach((seller) => {
        if (!isNaN(seller.nps) && seller.nps > 0) {
          avgNPS += parseFloat(seller.nps);
          cant++;
        }
      });
    }

    avgNPS > 0 ? (avgNPS /= cant) : (avgNPS = 0);
    setTotalNps(avgNPS.toFixed(2));
  };

  const getUserNamesOnline = () => {
    db_realtime.child("user-names-online").once("value", (registers) => {
      getOnline(registers);
      getOffline();
    });
  };
  /*eslint-disable */
  useEffect(() => {
    getUserNamesOnline();

    setInterval(() => getUserNamesOnline(), 60000);
  }, []);
  /*eslint-enable */

  const {
    user: { user },
  } = useContext(AuthContext);

  useEffect(() => {
    const get = async () => {
      const camps = await getCampaignsForCompany(user.idcompany);
      //console.log("camps", camps);
      setCamps(camps);
    };
    if (camps && camps.length === 0) {
      get();
    }
  }, [camps, user]);

  /*  
  useEffect(() => {
    const getCampaigns = async () => {
      
      //const camps = await getCampaigns();
      //setTotalCamps(camps.length);
      setTotalCamps(0);
    };

    getCampaigns();
  }, []);
*/
  useEffect(() => {
    document.getElementsByTagName("body")[0].style =
      "background-color: #ffffff";
  });

  const useStyles = makeStyles((theme) => ({
    root: {
      padding: "10px",
      marginBottom: "40px",
    },
    dataDiv: {
      display: "flex",
      "&> div": {
        width: "50%",
      },
    },
    iconTitle: {
      marginLeft: "0px",
      textAlign: "center",
    },
    personIcon: {
      color: "white",
      backgroundColor: "#FFC107",
      float: "left",
      marginLeft: "25%",
      marginRight: "10px",
    },
    npcIcon: {
      color: "white",
      backgroundColor: "tomato",
      float: "left",
      marginLeft: "25%",
      marginRight: "10px",
    },
    equalizerIcon: {
      color: "white",
      backgroundColor: "#00bcd4",
      float: "left",
      marginLeft: "25%",
      marginRight: "10px",
    },
    iconNumber: {
      textAlign: "center",
    },
    dataGrid: {
      marginBottom: "30px",
    },
    defaultPaper: {
      padding: "20px",
      minHeight: "200px",
    },
    offlineSellers: {
      marginTop: "40px",
    },
    linkButton: {
      textDecoration: "none",
      marginLeft: "45%",
      "&> svg": { marginTop: "10%" },
    },
    updateTime: {
      padding: "0",
      fontSize: ".8rem",
      fontWeight: "bold",
    },
  }));
  const classes = useStyles();

  const goToVendedor = (vendedor) => {
    history.push({
      pathname: "/seller",
      id: vendedor.uid,
      to2: "seller",
    });
  };

  return (
    <Container>
      {/*
      <Paper elevation={3} className={classes.root}>
        <h3>Now in Wow!</h3>
      </Paper>
*/}
      <Grid container spacing={1} className={classes.updateTime}>
        <Grid item xs={12}>
          <p>ltima actualizacin: {lastUpdate}</p>
        </Grid>
      </Grid>
      <Grid container spacing={3} className={classes.dataGrid}>
        <Grid item xs={12} sm={4}>
          <Paper elevation={3} className={classes.defaultPaper}>
            <p className={classes.iconTitle}>Campaigns</p>
            <Avatar className={classes.equalizerIcon}>
              <EqualizerIcon />
            </Avatar>
            <h2 className={classes.iconNumber}>{camps.length}</h2>

            <Link
              to={{
                pathname: "/",
                search: "",
                hash: "",
                state: { to: "campaigns" },
              }}
              className={classes.linkButton}
            >
              <VisibilityIcon fontSize="large" />
            </Link>
          </Paper>
        </Grid>
        <Grid item xs={12} sm={4}>
          <Paper elevation={3} className={classes.defaultPaper}>
            <p className={classes.iconTitle}>Online Sellers</p>
            <Avatar className={classes.personIcon}>
              <PersonIcon />
            </Avatar>
            <h2 className={classes.iconNumber}>{sellers.length}</h2>
            {/*
            <p className={classes.iconNumber}>{"0.00 % Online"}</p>
            */}
            <Link
              to={{
                pathname: "/",
                search: "",
                hash: "",
                state: { to: "sellers" },
              }}
              className={classes.linkButton}
            >
              <VisibilityIcon fontSize="large" />
            </Link>
          </Paper>
        </Grid>
        <Grid item xs={12} sm={4}>
          <Paper elevation={3} className={classes.defaultPaper}>
            <p className={classes.iconTitle}>NPS</p>
            <Avatar className={classes.npcIcon}>
              <StarIcon />
            </Avatar>
            <h2 className={classes.iconNumber}>{totalNps}</h2>
            <Link
              to={{
                pathname: "/",
                search: "",
                hash: "",
                state: { to: "sellers" },
              }}
              className={classes.linkButton}
            >
              <VisibilityIcon fontSize="large" />
            </Link>
          </Paper>
        </Grid>
      </Grid>

      <MaterialTable
        title="Online Sellers"
        style={{
          paddingLeft: "10px",
        }}
        columns={[
          {
            title: "Avatar",
            field: "avatar",
            editable: "always",
            render: (rowData) =>
              rowData.avatar ? (
                <img
                  src={rowData.avatar}
                  alt="avatar"
                  style={{ width: 50, borderRadius: "30%" }}
                />
              ) : (
                <Avatar>
                  {rowData.name
                    .match(/\b(\w)/g)
                    .join("")
                    .toUpperCase()}
                </Avatar>
              ),
            headerStyle: {
              width: "25%",
            },
          },
          {
            title: "Email",
            field: "mail",
            headerStyle: {
              width: "30%",
            },
          },
          {
            title: "Name",
            field: "name",
            editable: "never",
            headerStyle: {
              width: "30%",
            },
          },
          {
            title: "NPS",
            field: "nps",
            editable: "never",
            headerStyle: {
              width: "30%",
            },
          },
        ]}
        data={sellers}
        actions={[
          {
            icon: ArrowForwardIosIcon,
            tooltip: "Ver Vendedor",
            onClick: (event, rowData) => goToVendedor(rowData),
          },
        ]}
        options={{
          pageSize: 10,
          pageSizeOptions: [5, 10, 20],
        }}
      />

      {/* <div className={classes.offlineSellers}>
        <MaterialTable
          title="Offline Sellers"
          style={{
            paddingLeft: "10px",
          }}
          columns={[
            {
              title: "Avatar",
              field: "avatar",
              editable: "always",
              render: (rowData) =>
                rowData.avatar ? (
                  <img
                    src={rowData.avatar}
                    alt="avatar"
                    style={{ width: 50, borderRadius: "30%" }}
                  />
                ) : (
                  <Avatar>
                    {rowData.name
                      .match(/\b(\w)/g)
                      .join("")
                      .toUpperCase()}
                  </Avatar>
                ),
              headerStyle: {
                width: "25%",
              },
            },
            {
              title: "Email",
              field: "mail",
              headerStyle: {
                width: "30%",
              },
            },
            {
              title: "Name",
              field: "name",
              editable: "never",
              headerStyle: {
                width: "30%",
              },
            },
            {
              title: "NPS",
              field: "nps",
              editable: "never",
              headerStyle: {
                width: "30%",
              },
            },
          ]}
          data={offLineSellers}
          actions={[
            {
              icon: ArrowForwardIosIcon,
              tooltip: "Ver Vendedor",
              onClick: (event, rowData) => goToVendedor(rowData),
            },
          ]}
        />
      </div> */}
    </Container>
  );
};

export default Main;
