import React, { useState, Fragment } from "react";
import { List, ListItem, Collapse, Chip } from "@material-ui/core";
import ExpandLess from "@material-ui/icons/ExpandLess";
import ExpandMore from "@material-ui/icons/ExpandMore";
import StatsSeller from "./StatsSeller";

const StatsCampaign = ({ camp }) => {
  const [open, setOpen] = useState(false);

  const handleClick = () => {
    setOpen(!open);
  };
  return camp.sellers.length === 0 ? (
    <ListItem button>
      <div>{camp.name}</div>
      <Chip
        variant="outlined"
        size="small"
        label={`avrgQueryTime ${camp.avrgQueryTime}`}
      />
    </ListItem>
  ) : (
    <Fragment>
      <ListItem button onClick={handleClick}>
        <div>{camp.name}</div>

        {open ? <ExpandLess /> : <ExpandMore />}
      </ListItem>
      <Collapse in={open} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          {camp.sellers.map((s) => (
            <StatsSeller key={s.uid} seller={s} />
          ))}
        </List>
      </Collapse>
    </Fragment>
  );
};

export default StatsCampaign;
