import React, { useState, useContext, Fragment } from "react";
import { NavLink } from "react-router-dom";
import firebase from "../config/firebase";
import {
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  Button,
  Drawer,
  Grid,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Badge,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import MenuIcon from "@material-ui/icons/Menu";
import PersonIcon from "@material-ui/icons/Person";
import EqualizerIcon from "@material-ui/icons/Equalizer";
import PeopleAltIcon from "@material-ui/icons/PeopleAlt";
import ShowChartIcon from "@material-ui/icons/ShowChart";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
//import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ArrowDropDownCircleRoundedIcon from "@material-ui/icons/ArrowDropDownCircleRounded";
import SupervisorAccountIcon from "@material-ui/icons/SupervisorAccount";
//import ClearIcon from "@material-ui/icons/Clear";
import Avatar from "@material-ui/core/Avatar";
//import Badge from "@material-ui/core/Badge";
//import SettingsIcon from "@material-ui/icons/Settings";
import { AuthContext } from "../contexts/AuthContext";

const NavBar = (props) => {
  const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
      marginBottom: "40px",
    },
    appBar: { backgroundColor: "#16192f", minHeight: "65px", padding: "5px" },
    appBarProfile: {
      height: "70px",
      width: "100px",
      "&> span": {
        marginLeft: "25px",
        marginTop: "15px",
        "&> span": {
          borderRadius: "50%",
          backgroundColor: "white",
          border: "2px white solid",
          width: "50%",
          "&:hover": {
            cursor: "pointer",
          },
        },
      },
    },
    divProfile: {
      width: "60px",
    },
    imgProfile: {
      width: "100%",
      objectFit: "cover",
    },
    logOutAvatar: {
      width: "8px",
      height: "8px",
      backgroundColor: "#635a58",
      padding: "3px",
      border: "1px solid white",
      cursor: "pointer",
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
    clienteInfo: {
      backgroundColor: "#241332",
      height: "150px",
      width: "250px",
      borderRadius: "0px 50px 0px 50px",
      display: "flow-root",
      "&> div": {
        marginTop: "13%",
        marginBottom: "5%",
        marginLeft: "25px",
      },
      "&> h5": {
        color: "white",
        height: "fit-content",
        marginLeft: "25px",
        fontSize: "1rem",
      },
      "&> h6": {
        color: "white",
        height: "fit-content",
        marginLeft: "25px",
        fontSize: "0.7rem",
      },
    },
    title: {
      flexGrow: 1,
    },
    userMenu: {
      color: "white",
    },
    presentation: {
      borderRadius: "0px 50px 0px 50px",
    },
    navLink: {
      textDecoration: "none",
      color: "white",
      width: "30%",
    },
    headerLink: {
      textDecoration: "none",
      color: "white",
      width: "30%",
      "& img": {
        width: "50px",
        margin: "10px",
      },
    },
    menuList: {
      paddingTop: "60px",
      paddingLeft: "6px",
      paddingBottom: "8px",
    },
    sideMenuLink: {
      textDecoration: "none",
      color: "#241332",
      "&:hover": {
        backgroundColor: "#eceaec",
      },
    },
    sideMenuLinkIcon: {
      color: "#5f4d4d",
    },
    sideMenuDivider: {
      backgroundColor: "rgb(42, 72, 100)",
      height: "2px",
      marginBottom: "5px",
      marginTop: "5px",
    },
    logOutButton: {
      marginTop: "20%",
      "& svg": {
        transform: "rotate(180deg)",
        marginRight: "30px",
      },
    },
    floatSignOut: {
      width: 0,
      height: 0,
      marginLeft: "55%",
      borderStyle: "solid",
      borderWidth: "0 5px 10px 5px",
      borderColor: "transparent transparent #161930 transparent",
    },
    floatSignOutContent: {
      border: "1px solid blue",
      borderRadius: "5px 5px 5px 5px",
      backgroundColor: "#161930",
      display: "flex",
      "& svg": {
        width: "20px",
        transform: "rotate(180deg)",
      },
      "& h6": {
        margin: "5px",
      },
      "&:hover": {
        backgroundColor: "tomato",
        cursor: "pointer",
      },
    },
  }));
  const classes = useStyles();

  const [menuOpen, setMenuOpen] = useState(false);
  const {
    user: { user },
    campaign,
    dispatch,
  } = useContext(AuthContext);
  const [showLogOut, setShowLogOut] = useState(false);

  const handleSignout = () => {
    firebase
      .auth()
      .signOut()
      .then(() => {
        localStorage.removeItem("user");
        localStorage.removeItem("lastRoute");
        localStorage.removeItem("idSelected");
        dispatch({
          type: "SIGNOUT_SUCCESS",
        });
      });
  };

  const floatSignOut = (
    <div>
      <div className={classes.floatSignOut}></div>
      <div className={classes.floatSignOutContent}>
        <ExitToAppIcon />
        <h6 onClick={handleSignout}>Log Out</h6>
      </div>
    </div>
  );

  return (
    <Fragment>
      <div className={classes.root}>
        <AppBar position="static" className={classes.appBar}>
          <Toolbar className="root-menu" variant="dense">
            <IconButton
              edge="start"
              className={classes.menuButton}
              color="inherit"
              aria-label="menu"
              onClick={() => setMenuOpen(true)}
            >
              <MenuIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              <NavLink
                to={{
                  pathname: "/",
                  search: "",
                  hash: "",
                  state: { to: "/" },
                }}
                className={classes.headerLink}
              >
                <img src="./imgs/wow.png" alt="wow" />
              </NavLink>
            </Typography>
            <div className={classes.appBarProfile}>
              <Badge
                overlap="circle"
                anchorOrigin={{
                  vertical: "bottom",
                  horizontal: "right",
                }}
                badgeContent={<ArrowDropDownCircleRoundedIcon color="action" />}
                onClick={() => setShowLogOut(!showLogOut)}
              >
                {user.avatar ? (
                  <Avatar alt="profile" src={user.avatar} />
                ) : (
                  <Avatar alt="profile">
                    {user.name
                      .match(/\b(\w)/g)
                      .join("")
                      .toUpperCase()}
                  </Avatar>
                )}
              </Badge>
              {showLogOut && floatSignOut}
            </div>
          </Toolbar>
        </AppBar>
      </div>
      <Drawer open={menuOpen} onClose={() => setMenuOpen(false)}>
        <div
          role="presentation"
          className={classes.presentation}
          onClick={() => setMenuOpen(false)}
          onKeyDown={() => setMenuOpen(false)}
        >
          <Grid container justify="center" className={classes.clienteInfo}>
            {user.avatar ? (
              <Avatar alt="profile" src={user.avatar} />
            ) : (
              <Avatar alt="profile">
                {user.name
                  .match(/\b(\w)/g)
                  .join("")
                  .toUpperCase()}
              </Avatar>
            )}
            <Typography variant="h5">{user && user.name}</Typography>

            <Typography variant="h6">
              {user && user.admin ? "Admin" : campaign.name}
            </Typography>
          </Grid>

          <List className={classes.menuList}>
            <NavLink
              to={{
                pathname: "/",
                search: "",
                hash: "",
                state: { to: "sellers" },
              }}
              className={classes.sideMenuLink}
            >
              <ListItem button key={"Sellers"}>
                <ListItemIcon className={classes.sideMenuLinkIcon}>
                  <PersonIcon />
                </ListItemIcon>
                <ListItemText primary={"Sellers"} />
              </ListItem>
            </NavLink>
            <NavLink
              to={{
                pathname: "/",
                search: "",
                hash: "",
                state: { to: "campaigns" },
              }}
              className={classes.sideMenuLink}
            >
              <ListItem button key={"Campaigns"}>
                <ListItemIcon className={classes.sideMenuLinkIcon}>
                  <EqualizerIcon />
                </ListItemIcon>
                <ListItemText primary={"Campaigns"} />
              </ListItem>
            </NavLink>
            <NavLink
              to={{
                pathname: "/",
                search: "",
                hash: "",
                state: { to: "reports" },
              }}
              className={classes.sideMenuLink}
            >
              <ListItem button key={"Reports"}>
                <ListItemIcon className={classes.sideMenuLinkIcon}>
                  <ShowChartIcon />
                </ListItemIcon>
                <ListItemText primary={"Reports"} />
              </ListItem>
            </NavLink>
            {user.admin && (
              <NavLink
                to={{
                  pathname: "/",
                  search: "",
                  hash: "",
                  state: { to: "users" },
                }}
                className={classes.sideMenuLink}
              >
                <ListItem button key={"Clients"}>
                  <ListItemIcon className={classes.sideMenuLinkIcon}>
                    <PeopleAltIcon />
                  </ListItemIcon>
                  <ListItemText primary={"Clients"} />
                </ListItem>
              </NavLink>
            )}

            <NavLink
              to={{
                pathname: "/",
                search: "",
                hash: "",
                state: { to: "admins" },
              }}
              className={classes.sideMenuLink}
            >
              <ListItem button key={"Admins"}>
                <ListItemIcon className={classes.sideMenuLinkIcon}>
                  <SupervisorAccountIcon />
                </ListItemIcon>
                <ListItemText primary={"Admins"} />
              </ListItem>
            </NavLink>
          </List>
        </div>
        <Button className={classes.logOutButton} onClick={handleSignout}>
          <ExitToAppIcon />
          <h4>Log Out</h4>
        </Button>
      </Drawer>
    </Fragment>
  );
};

export default NavBar;

