import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import ListSubheader from "@material-ui/core/ListSubheader";
import List from "@material-ui/core/List";
import StatsCampaign from "./StatsCampaign";

const StatsReportDetails = (props) => {
  const history = useHistory();
  const { camps } = history.location.state;
  const [open, setOpen] = useState(true);

  useEffect(() => {
    for (let i = 0; i < camps.length; i++) {
      const camp = camps[i];

      if (camp.hasOwnProperty("sellers")) {
        var avrgQueryTime = 0;
        var adherence = 0;
        var effectiviness = 0;
        var attention2 = 0;
        var dropouts1 = 0;
        var droputs2 = 0;
        var entries = 0;
        var service1 = 0;
        var service2 = 0;
        var service3 = 0;

        for (let s = 0; s < camp.sellers.length; s++) {
          const sel = camp.sellers[s];

          avrgQueryTime += sel["avrgQueryTime"];
          adherence += sel["adherence"];
          effectiviness += sel["effectiviness"];
          attention2 += sel["attention2"];
          dropouts1 += sel["dropouts1"];
          droputs2 += sel["droputs2"];
          entries += sel["entries"];
          service1 += sel["service1"];
          service2 += sel["service2"];
          service3 += sel["service3"];
        }

        camp["avrgQueryTime"] = avrgQueryTime;
        camp["adherence"] = adherence;
        camp["effectiviness"] = effectiviness;
        camp["attention2"] = attention2;
        camp["dropouts1"] = dropouts1;
        camp["droputs2"] = droputs2;
        camp["entries"] = entries;
        camp["service1"] = service1;
        camp["service2"] = service2;
        camp["service3"] = service3;
      }
    }
  }, [camps]);

  const useStyles = makeStyles((theme) => ({
    root: {
      width: "100%",
      maxWidth: 360,
      backgroundColor: theme.palette.background.paper,
    },
  }));
  const classes = useStyles();
  console.log("campaigns", camps);
  return (
    <List
      component="nav"
      aria-labelledby="nested-list-subheader"
      subheader={
        <ListSubheader component="div" id="nested-list-subheader">
          Nested List Items
        </ListSubheader>
      }
      className={classes.root}
    >
      {camps.map((c) => (
        <StatsCampaign key={c.id} camp={c} />
      ))}
    </List>
  );
};

export default StatsReportDetails;
