import React from "react";
import { Avatar, Paper, Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const NpsReport = (props) => {
  const { company, bestSeller, bestCampaign } = props;

  const useStyles = makeStyles((theme) => ({
    reportContainer: {
      paddingBottom: "40px",
      minHeight: "400px",
    },
    resume: {
      marginBottom: "40px",
      padding: "10px",
      "&> h2": {
        fontFamily: "Verdana, Arial, Helvetica, sans-serif",
        textAlign: "center",
      },
    },
    ItemTitle: {
      fontFamily: "Verdana, Arial, Helvetica, sans-serif",
      fontWeight: "900",
    },
    resumeTotal: {
      textAlign: "center",
      padding: "5px",
      height: "202px",
      "&> div": {
        marginLeft: "35%",
        width: "100px",
        height: "100px",
      },
      "&> div.nps": {
        fontSize: "50px",
        backgroundColor: "darkorange",
      },
    },
    resumeBestCampaign: {
      textAlign: "center",
      padding: "5px",
      height: "202px",
      "&> div": {
        marginLeft: "37%",
        width: "100px",
        height: "100px",
      },
      "&> div.nps": {
        fontSize: "50px",
        backgroundColor: "darkorange",
      },
    },
    resumeBestSeller: {
      textAlign: "center",
      padding: "5px",
      height: "202px",
      "&> div": {
        marginLeft: "44%",
      },
      "&> div.npsSeller": {
        backgroundColor: "darkorange",
        padding: "5px",
        marginLeft: "44%",
      },
    },
  }));
  const classes = useStyles();

  return (
    <Grid container spacing={3}>
      <Grid item xs={12} sm={4}>
        <Paper className={classes.resumeTotal}>
          <p className={classes.ItemTitle}>Total</p>
          <p>{company.name}</p>
          <Avatar className="nps">{(company["nps"]).toFixed(2)}</Avatar>
        </Paper>
      </Grid>
      <Grid item xs={12} sm={4}>
        <Paper className={classes.resumeBestCampaign}>
          <p className={classes.ItemTitle}>Best Campaign</p>
          <p>{bestCampaign.name}</p>
          <Avatar className="nps">{(bestCampaign.nps).toFixed(2)}</Avatar>
        </Paper>
      </Grid>
      <Grid item xs={12} sm={4}>
        <Paper className={classes.resumeBestSeller}>
          <p className={classes.ItemTitle}>Best Seller</p>
          <Avatar className={classes.avatar} alt="avatar">
            {bestSeller && bestSeller.avatar ? (
              <img src={bestSeller.avatar} alt="avatar" />
            ) : (
              bestSeller.name
                .match(/\b(\w)/g)
                .join("")
                .toUpperCase()
            )}
          </Avatar>
          <h4>{bestSeller.name}</h4>
          <Avatar className="npsSeller">{(bestSeller.nps).toFixed(2)}</Avatar>
        </Paper>
      </Grid>
    </Grid>
  );
};

export default NpsReport;

