export const getSellersForCampaign = async (id) => {
  if (apiVer === 1)
    return axios
      .get(`${apiUrl}/users.json`)
      .then((res) => {
        const result = res.data;
        console.log("users --> ", result);
        console.log(res);
        var sellers = [];
        for (let i in result) {
          if (result[i].vendedor)
            if (result[i].hasOwnProperty("retailers"))
              if (result[i]["retailers"].hasOwnProperty(id))
                sellers.push({
                  id: i,
                  name: result[i].name,
                  avatar: result[i].photo,
                  email: result[i].mail,
                  password: "********",
                  nps: result[i].ratemedia,
                });
        }
        console.log("sellers --> ", sellers);
        return sellers;
      })
      .catch((error) => {
        console.log("error", error);
      });

  return axios
    .get(`${apiUrl}users/listUsers/Campaign/${id}`)
    .then((res) => {
      const result = res.data;
      //console.log("seller for campaign --> ", result);

      return result;
    })
    .catch((error) => {
      console.log("error", error);
    });
};

