export const getOnlineSellers = (users = null, idcompany = null) => {
  if (apiVer === 1)
    return axios
      .get(`https://xinnux-chat-testing.firebaseio.com/user-names-online.json`)
      .then((res) => {
        const online = res.data;
        //console.log("users online --> ", online);

        if (online) {
          return axios
            .get(`https://xinnux-chat-testing.firebaseio.com/users.json`)
            .then((res) => {
              const result = res.data;
              var sellers = [];
              for (let i in online) {
                const subId = Object.keys(online[i]);
                const idSel = online[i][subId[0]].id;
                const sel = result[idSel];
                //console.log("sel ->>", sel);
                sellers.push({
                  id: i,
                  name: sel.name,
                  avatar: sel.photo,
                  email: sel.mail,
                  password: "********",
                  nps: sel.ratemedia,
                });
              }
              //console.log("sellers --> ", sellers);
              return sellers;
            })
            .catch((error) => {
              console.log("error", error);
            });
        }
      })
      .catch((error) => {
        console.log("error", error);
      });
  else {
    return axios
      .get(`${apiUrl}users/seller-list/list`)
      .then((res) => {
        const result = res.data;
        let retorno = [];

        result.forEach((ret) => {
          users.forEach((user) => {
            user.forEach((data) => {
              if (data.val().id === ret.uid && ret.idcompany === idcompany)
                retorno.push(ret);
            });
          });
        });

        return retorno;
      })
      .catch((error) => {
        console.log("error", error);
      });
  }
};

export const getOfflineSellers = (id) => {
  if (apiVer === 1)
    return axios
      .get(`https://xinnux-chat-testing.firebaseio.com/user-names-online.json`)
      .then((res) => {
        const online = res.data;
        //console.log("users online --> ", online);

        return axios
          .get(`https://xinnux-chat-testing.firebaseio.com/users.json`)
          .then((res) => {
            const result = res.data;
            var sellers = [];
            for (let i in online) {
              delete result[i];
            }

            for (let i in result) {
              if (result[i].vendedor)
                sellers.push({
                  id: i,
                  name: result[i].name,
                  avatar: result[i].photo,
                  email: result[i].mail,
                  password: "********",
                  nps: result[i].ratemedia,
                });
            }
            //console.log("offline sellers --> ", sellers);
            return sellers;
          })
          .catch((error) => {
            console.log("error", error);
          });
      })
      .catch((error) => {
        console.log("error", error);
      });
  else
    return axios
      .get(`${apiUrl}users/seller-list/list`)
      .then((res) => {
        const result = res.data;
        let retorno = [];

        result.forEach((ret) => {
          if (ret.idcompany === id)
            db_realtime
              .child("user-names-online/" + ret.name.toLowerCase())
              .once("value", (user) => {
                if (!user.exists()) retorno.push(ret);
              });
        });

        return retorno;
      })
      .catch((error) => {
        console.log("error", error);
      });
};

export const getSeller = (id) => {
  if (apiVer === 1)
    return axios
      .get(`${apiUrl}users.json`)
      .then((res) => {
        const sellers = res.data;
        //console.log("seller --> ", sellers[id]);
        var sel = {
          id: id,
          name: sellers[id].name,
          avatar: sellers[id].photo,
          email: sellers[id].mail,
          password: "********",
          nps: sellers[id].ratemedia,
        };

        if (sellers[id].hasOwnProperty("retailers")) {
          const retailers = sellers[id]["retailers"];
          var aux = [];
          for (let i in retailers) {
            aux.push({
              id: i,
              name: i,
              avatar: "./imgs/moto.png",
              headerColor: "#00bcd4",
              fontColor: "#00bcd4",
              backgroundImage: "url",
              nps: retailers[i].rate,
              loadText: "Looking for a Seller",
              typeLogin: 1,
              online: 0,
            });
          }
        }

        sel["campaigns"] = aux;

        return sel;
      })
      .catch((error) => {
        console.log("error", error);
      });
  else
    return axios
      .get(`${apiUrl}users/user/${id}`)
      .then((res) => {
        const seller = res.data;
        //console.log("seller --> ", seller);

        return seller;
      })
      .catch((error) => {
        console.log("error", error);
      });
};

export const getSellers = (id) => {
  if (apiVer === 1)
    return axios
      .get(`/users.json`, {
        headers: {
          "Access-Control-Allow-Origin": "*",
        },
      })
      .then((res) => {
        const data = res.data;
        //console.log("data --> ", data);
        var sellers = [];
        for (let i in data) {
          if (data[i].vendedor)
            sellers.push({
              id: i,
              name: data[i].name,
              avatar: data[i].photo,
              email: data[i].mail,
              password: "********",
              nps: data[i].ratemedia,
            });
        }
        //console.log("all sellers --> ", data);
        return sellers;
      })
      .catch((error) => {
        console.log("error", error);
      });
  else
    return axios
      .get(`${apiUrl}users/seller-list/list `)
      .then((res) => {
        let data = [];

        res.data.forEach((seller) => {
          if (seller.idcompany === id) data.push(seller);
        });

        return data;
      })
      .catch((error) => {
        console.log("error", error);
      });
};

