export const getUserCampaigns = (camps) => {
  /*return axios
    .get(`${apiUrl}campaign/userCampaigns/list/${uid}`, {
      headers: { "Access-Control-Allow-Origin": "*" },
    })
    .then((res) => {
      const data = res.data;
      console.log("user campaigns --> ", data);
      return data;
    })
    .catch((error) => {
      console.log("error", error);
      return error;
    });*/
  return db_firestore
    .collection("campaings")
    .get()
    .then((resp) => {
      const snaps = resp.docs;

      var aux = [];
      snaps.map((snap) => {
        const idDoc = snap.id;
        if (camps && camps.length > 0)
          if (camps.includes(idDoc)) {
            const data = snap.data();
            aux.push({ ...data, id: idDoc });
          }
        return null;
      });
      //console.log("aux", aux);
      return aux;
    });
};

