import React, { useEffect, useState, useContext } from "react";
import { useHistory } from "react-router-dom";
import {
  Container,
  Input,
  MenuItem,
  FormControl,
  Select,
  Avatar,
  Button,
} from "@material-ui/core";
import MaterialTable from "material-table";
//import { db_firestore } from "../config/firebase";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import { makeStyles } from "@material-ui/core/styles";
import CallMadeIcon from '@material-ui/icons/CallMade';
//import axios from "axios";
import { getCampaigns } from "../middleware/Middleware";
import { AuthContext } from "../contexts/AuthContext";
import { ButtonContainer } from "../floating-button/components/app.jsx";

const Campaigns = (props) => {
  const [camps, setCamps] = useState([]);
  const [showGenerator, setButtonBehaviour] = useState(false);
  const [iframeContent, setIframeContent] = useState(null);
  //const apiUrl = process.env.REACT_APP_URL_API;

  const history = useHistory();

  const {
    user: { user },
  } = useContext(AuthContext);

  const getCamps = async () => {
    const camps = await getCampaigns(user.idcompany);
    console.log("camps", camps);
    setCamps(camps);
  };
  /*eslint-disable */
  useEffect(() => {
    //if (camps && camps.length === 0) {
    getCamps();
    //}
  }, []);
  /*eslint-enable */

  const useStyles = makeStyles((theme) => ({
    campaignsTable: {
      marginTop: "40px",
    },
    selectInput: {
      width: "100%",
    },
    uploadFile: {
      borderRadius: "30%",
      cursor: "pointer",
      "&> img": {
        width: "100%",
        objectFit: "cover",
      },
    },
  }));
  const classes = useStyles();

  const [file, setFile] = useState();

  const uploadImage = (e) => {
    const file = e.target.files[0];
    if (file.type.indexOf("image") < 0) {
      setFile();
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onloadend = function (e) {
      setFile(reader.result);
    };
  };

  const goToCampaign = (camp) => {
    history.push({
      pathname: "/",
      id: camp.id,
      to2: "campaign",
    });
  };

  return (
    <Container className={classes.campaignsTable}>
      <MaterialTable
        title="Campaigns"
        columns={[
          {
            title: "Name",
            field: "name",
            editable: "always",
            headerStyle: {
              width: "30%",
              textAlign: "center",
            },
          },
          {
            title: "Avatar",
            field: "avatar",
            editable: "always",
            render: (rowData) => (
              <img
                src={rowData.avatar ? rowData.avatar : "./imgs/wow.png"}
                alt="avatar"
                style={{ width: 50, borderRadius: "50%" }}
              />
            ),
            editComponent: (props) => (
              <div>
                <Avatar className={classes.uploadFile}>
                  <img
                    src={file ? file : "./imgs/fileUpload.png"}
                    alt="avatar"
                    onClick={() =>
                      document.getElementById("fileUpload").click()
                    }
                  />
                </Avatar>
                <Input
                  type="file"
                  id="fileUpload"
                  name="file"
                  style={{
                    display: "none",
                  }}
                  inputProps={{ accept: "image/*" }}
                  //onChange={(e) => props.onChange(e.target.value)}
                  onChange={(e) => uploadImage(e)}
                />
              </div>
            ),
            headerStyle: {
              width: "25%",
            },
          },
          {
            title: "Type Login",
            field: "typeLogin",
            editable: "always",
            headerStyle: {
              width: "30%",
              textAlign: "center",
            },
            render: (rowData) => {
              if (rowData.typeLogin === 1) return <div>Email and Pass</div>;
              else if (rowData.typeLogin === 2) return <div>Just Name</div>;
              else return <div>Just Email</div>;
            },
            editComponent: (props) => (
              <FormControl className={classes.selectInput}>
                <Select
                  labelId="typeLogin-select"
                  label="Login Type"
                  defaultValue={1}
                  id="typeLogin-select"
                  onChange={(e) => props.onChange(e.target.value)}
                >
                  <MenuItem key={1} value={"1"}>
                    Email and Pass
                  </MenuItem>
                  <MenuItem key={2} value={"2"}>
                    Just Name
                  </MenuItem>
                  <MenuItem key={3} value={"3"}>
                    Just Email
                  </MenuItem>
                </Select>
              </FormControl>
            ),
          },
          {
            title: "NPS",
            field: "nps",
            editable: "never",
            headerStyle: {
              width: "30%",
            },
          },
          {
            title: "URL Sales",
            field: "URL promotor",
            editable: "never",
            headerStyle: {
              width: "30%",
            },
            render: (rowData) => {
              let url = 'https://chat.wow2.xinnux.com/'+user.name.toLowerCase().split(' ').join('') +'/sales'              
              return(
                <Button>
                  <CallMadeIcon
                    onClick={ e => window.open(url, '_blank')}
                  />
                </Button>
              )
            }
          },
          {
            title: "URL Cliente",
            field: "URL cliente",
            editable: "never",
            headerStyle: {
              width: "30%",
            },
            render: (rowData) => {
              let url = 'https://chat.wow2.xinnux.com/'+user.name.toLowerCase().split(' ').join('') +'/'+rowData.name.toLowerCase().split(' ').join('') +'/client'
              return(
                <Button>
                  <CallMadeIcon
                    onClick={ e => window.open(url, '_blank') }/>
                </Button>
              )
            }
          },
          {
            title: "Embed Button",
            field: "button",
            editable: "never",
            headerStyle: {
              width: "30%",
            },
            render: (rowData) => {
              return (
                <Button
                  variant="contained"
                  color="primary"
                  onClick={() => {
                    setButtonBehaviour(true);
                    setIframeContent(rowData.name);
                  }}
                >
                  Generate
                </Button>
              );
            },
          },
        ]}
        data={camps}
        actions={[
          {
            icon: ArrowForwardIosIcon,
            tooltip: "Edit Campaign",
            onClick: (event, rowData) => goToCampaign(rowData),
          },
        ]}
        options={{
          pageSize: 10,
          pageSizeOptions: [5, 10, 20],
        }}
      />
      {showGenerator ? (
        <ButtonContainer
          event={() => setButtonBehaviour(false)}
          campaign={iframeContent}
          company={user.name}
        />
      ) : null}
    </Container>
  );
};

export default Campaigns;

