import React, { useState, useEffect, useContext } from "react";
import { Container, Avatar, Input } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import MaterialTable from "material-table";
//import axios from "axios";
//import { db_firestore } from "../config/firebase";
import {
  getClients,
  getCampaign,
  getCampaignsForCompany,
} from "../middleware/Middleware";
import { AuthContext } from "../contexts/AuthContext";

const Users = () => {
  //const apiUrl = process.env.REACT_APP_URL_API;
  const [users, setUsers] = useState(null);
  const [campaigns, setCampaigns] = useState(null);
  const {
    user: { user },
  } = useContext(AuthContext);

  const get = async () => {
    const data = await getClients(user.idcompany);
    setUsers(data);
  };

  const getCamps = async () => {
    const camps = await getCampaignsForCompany(user.idcompany);
    let infoCamp = [];
    camps.forEach(async (camp) => {
      let campaign = await getCampaign(camp);
      infoCamp.push(campaign);
    });
    console.log("camps", infoCamp);
    setCampaigns(infoCamp);
  };

  /*eslint-disable */
  useEffect(() => {
    if (!users) get();
    if (!campaigns) getCamps();
  }, [users]);
  /*eslint-enable */

  const [file, setFile] = useState();

  const uploadImage = (e) => {
    const file = e.target.files[0];
    if (file.type.indexOf("image") < 0) {
      setFile();
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onloadend = function (e) {
      setFile(reader.result);
    };
  };

  const useStyles = makeStyles((theme) => ({
    sellersTable: {
      marginTop: "40px",
    },
    popList: {
      width: "100%",
    },
    uploadFile: {
      borderRadius: "30%",
      cursor: "pointer",
      "&> img": {
        width: "100%",
        objectFit: "cover",
      },
    },
  }));
  const classes = useStyles();

  if (!users || !campaigns) return <div>Loading</div>;
  return (
    <Container>
      <MaterialTable
        title="Clients"
        style={{ marginBottom: "50px", width: "102%" }}
        columns={[
          {
            title: "Avatar",
            field: "avatar",
            editable: "never",
            render: (rowData) =>
              rowData.avatar ? (
                <img
                  src={rowData.avatar}
                  alt="avatar"
                  style={{ width: 50, borderRadius: "30%" }}
                />
              ) : (
                <Avatar>
                  {rowData.name
                    .match(/\b(\w)/g)
                    .join("")
                    .toUpperCase()}
                </Avatar>
              ),
            editComponent: (props) => (
              <div>
                <Avatar className={classes.uploadFile}>
                  <img
                    src={file ? file : "./imgs/fileUpload.png"}
                    alt="avatar"
                    onClick={() =>
                      document.getElementById("fileUpload").click()
                    }
                  />
                </Avatar>
                <Input
                  type="file"
                  id="fileUpload"
                  name="file"
                  style={{
                    display: "none",
                  }}
                  inputProps={{ accept: "image/*" }}
                  onChange={(e) => uploadImage(e)}
                />
              </div>
            ),
            headerStyle: {
              width: "25%",
            },
          },
          {
            title: "Email",
            field: "mail",
            editable: "never",
            headerStyle: {
              width: "30%",
            },
            render: (rowData) => (
              <div>{rowData.mail || "Usuario no registrado"}</div>
            ),
          },
          {
            title: "Password",
            field: "password",
            editable: "never",
            headerStyle: {
              width: "30%",
            },
          },
          {
            title: "Name",
            field: "name",
            editable: "never",
            headerStyle: {
              width: "30%",
            },
            cellStyle: {
              padding: "0px",
            },
          },
          {
            title: "Campaign",
            field: "campaigns",
            editable: "never",
            headerStyle: {
              width: "30%",
              textAlign: "center",
            },
            render: (rowData) => (
              <div>
                {rowData && rowData !== undefined ? (
                  rowData.campaigns.map((camp) => <span>{camp.name}</span>)
                ) : (
                  <span> {"Not found"} </span>
                )}
              </div>
            ),
          },
        ]}
        data={users}
        options={{
          pageSize: 10,
          pageSizeOptions: [5, 10, 20],
        }}
      />
    </Container>
  );
};

export default Users;
