import React, { useState, useEffect, useContext } from "react";
//import { db_firestore } from "../config/firebase";
import { useHistory } from "react-router-dom";
import {
  getCampaign,
  getSellers,
  getResourses,
  addSellerToCampaign,
  getUsersCampaign,
} from "../middleware/Middleware";
import {
  Container,
  Paper,
  Avatar,
  Input,
  Grid,
  TextField,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  IconButton,
} from "@material-ui/core";
import CircularProgress from "@material-ui/core/CircularProgress";
import { makeStyles, withStyles } from "@material-ui/core/styles";
//import PersonIcon from "@material-ui/icons/Person";
//import StarIcon from "@material-ui/icons/Star";
//import HelpOutlineIcon from "@material-ui/icons/HelpOutline";
import { Save } from "@material-ui/icons";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import MaterialTable from "material-table";
import Tooltip from "@material-ui/core/Tooltip";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import HelpIcon from "@material-ui/icons/Help";
import Snackbar from "@material-ui/core/Snackbar";
import Delete from "@material-ui/icons/Delete";
import MuiAlert from "@material-ui/lab/Alert";
import SearchSellers from "./SearchSellers";
import axios from "axios";
import { BlockPicker } from "react-color";
import { AuthContext } from "../contexts/AuthContext";

const Campaign = (props) => {
  const history = useHistory();

  let id = history.location.id;
  if (!id || id === "undefined") {
    id = localStorage.getItem("idSelected");
    if (!id || id === "undefined") history.push("/");
  }

  const apiUrl = process.env.REACT_APP_URL_API;
  const [camp, setCamp] = useState();
  const [sellers, setSellers] = useState([]);
  const [allSellers, setAllSellers] = useState([]);
  const [sellersCampaign, setSellersCampaign] = useState([]);
  const [sellersCompany, setSellersCompany] = useState([]);
  const [resources, setResources] = useState([]);
  const [file, setFile] = useState();
  const [fileBanner, setFileBanner] = useState();
  const [fileBackground, setFileBackground] = useState();
  const [open, setOpen] = React.useState(false);

  const {
    user: { user },
  } = useContext(AuthContext);

  const handleChange = (event, name) => {
    setCamp({ ...camp, [name]: event.target.value });
  };

  const handleChangeColor = (color) => {
    setCamp({ ...camp, headerColor: color.hex });
  };

  const handleChangeColorFont = (color) => {
    setCamp({ ...camp, fontColor: color.hex });
  };

  const handleBackColor = (color) => {
    setCamp({ ...camp, backgroundColor: color.hex});
  }

  const getClientCampaing = async (id) => {
    var camp = await getCampaign(id);
    //console.log("camp ----->", camp);
    setCamp(camp);
    return false;
  };

  const getSellersCampaign = (id, allSellers) => {
    //var sellers = await getSellersForCampaign(id, allSellers);
    var res = [];

    for (const key in allSellers) {
      if (allSellers[key].hasOwnProperty("campaigns")) {
        const campaigns = allSellers[key].campaigns;
        if (campaigns.some((c) => c.id === id)) res.push(allSellers[key]);
      }
    }
    setSellers(res);
  };

  const getAllSellers = async () => {
    var sellersAll = await getSellers(user.idcompany);
    var aux = sellersAll.filter((s) => s.hasOwnProperty("mail"));
    aux = aux.filter((s) => {
      for (const sel in sellers) {
        if (sel.id === s.id || !s.hasOwnProperty("mail")) return false;
      }

      return true;
    });

    //console.log("aux", aux);
    setAllSellers(aux);
    getSellersCampaign(id, aux);
    getSellersCompany(id, aux);
  };

  const getSellersCompany = async (id, allSellers) => {
    let res = [];

    for (const key in allSellers) {
      if (allSellers[key].hasOwnProperty("campaigns")) {
        const campaigns = allSellers[key].campaigns;
        let finded = false;
        campaigns.forEach((camp) => {
          if (camp.id === id) finded = true;
        });
        if (!finded) res.push(allSellers[key]);
      }
    }
    setSellersCompany(res);
  };

  const addNewSeller = (res, sellerId) => {
    if (res.statusText === "OK") {
      setSellers([...sellers, allSellers.filter((s) => s.uid === sellerId)[0]]);
      getSellersForCampaign();
    }
  };

  const addSeller = async (sellerId) => {
    const promise = await addSellerToCampaign(id, sellerId);
    Promise.resolve(promise).then((res) =>
      Promise.resolve(addNewSeller(res, sellerId))
    );
    //if (resp.statusText === "OK") getSellersCampaign(id, allSellers);
  };

  //Tool Tip Info theme
  const HtmlTooltip = withStyles((theme) => ({
    tooltip: {
      backgroundColor: "#f5f5f9",
      color: "rgba(0, 0, 0, 0.87)",
      maxWidth: 220,
      maxHeight: 220,
      fontSize: theme.typography.pxToRem(12),
      border: "1px solid #dadde9",
    },
  }))(Tooltip);

  const getSellersForCampaign = async () => {
    const ress = await getUsersCampaign(id);
    let sellers = [];
    ress.map((sel) => {
      if (sel.role == "seller") sellers.push(sel);
    });
    setSellersCampaign(sellers);
    return false;
  };

  const getResoursesforCampaign = async () => {
    const ress = await getResourses(id);
    setResources(ress);
    return false;
  };

  useEffect(() => {
    if (id && id !== "undefined") {
      if (!camp) {
        getClientCampaing(id);
      }

      if (allSellers.length === 0) {
        getSellersForCampaign();
      }

      if (resources.length === 0) {
        getResoursesforCampaign();
      }

      getAllSellers();
    }
  }, [id]);

  //SnackBar
  function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
  }

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen(false);
  };

  
  const useStyles = makeStyles((theme) => ({
    campaniaInfo: {
      width: "100%",
      paddingBottom: "10px",
      marginBottom: "20px",
      borderRadius: "0px 50px 0px 0px",
    },
    dataDiv: {
      display: "flex",
      "&> div": {
        width: "50%",
      },
    },
    defaultGrid: {
      display: "flex",
      "&> div": {
        marginLeft: "20%",
      },
    },
    columnGrid:{
      display: 'flex',
      flexDirection: 'column'
    },
    textInputTitle: {
      marginLeft: "10%",
      "& label": {
        fontSize: "26px",
      },
      "& div": {
        fontSize: "21px",
      },
    },
    textInputLoadText: {
      "& label": {
        fontSize: "26px",
      },
      "& div": {
        fontSize: "21px",
        color: '#0a0a0a',
      },
    },
    selectInput: {
      marginLeft: "10%",
      marginTop: "15px",
      width: "40%",
    },
    selectInput2: {
      width: "100%",
    },
    campAvatar: {
      width: "130px",
      height: "130px",
      marginRight: "200px",
      "&> img": {
        width: "100%",
        objectFit: "cover",
      },
    },
    imgPreview: {},
    iconTitle: {
      marginLeft: "0px",
    },
    personIcon: {
      color: "white",
      backgroundColor: "#FFC107",
      float: "left",
      marginLeft: "20%",
    },
    npcIcon: {
      color: "white",
      backgroundColor: "tomato",
      float: "left",
      marginLeft: "25%",
    },
    iconNumber: {},
    searchVendedor: {
      padding: "10px",
      width: "92.5%",
      marginTop: "20px",
      marginBottom: "20px",
      marginLeft: "2%",
    },
    saveButton: {
      marginLeft: "50%",
    },
    defaultPaper: {
      padding: "20px",
      height: "100px",
      textAlign: "center",
    },
    headerDiv: {
      backgroundColor: "#241332",
      borderRadius: "0px 50px 0px 50px",
      color: "white",
      textAlign: "center",
      height: "60px",
      fontSize: "40px",
      letterSpacing: "4px",
    },
    headerDivCampaign: {
      backgroundColor: "#241332",
      borderRadius: "0px 50px 0px 50px",
      color: "white",
      textAlign: "center",
      height: "60px",
      fontSize: "40px",
      letterSpacing: "4px",
      display: "flex",
      "&> p": {
        margin: "0px",
        marginLeft: "25%",
      },
      "&> span": {
        marginLeft: "15%",
        color: "white",
      },
    },
    circularLoading: {
      marginLeft: "46%",
      marginTop: "20%",
      "& div": {
        color: "#16192f",
      },
    },
  }));
  const classes = useStyles();

  const goToSeller = (seller) => {
    history.push({
      pathname: "/",
      id: seller.uid,
      to2: "seller",
    });
  };

  const uploadImage = (e) => {
    const file = e.target.files[0];
    if (file.type.indexOf("image") < 0) {
      setFile();
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onloadend = function (e) {
      setFile(reader.result);
    };
  };

  const removeImage = (e) => {
    setFile();

    let newCampValues = camp;
    newCampValues.avatar = null;
  
    setCamp(newCampValues);
  }

  const uploadBannerImage = (e) => {
    const file = e.target.files[0];
    if (file.type.indexOf("image") < 0) {
      setFileBanner();
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onloadend = function (e) {
      setFileBanner(reader.result);
    };
  };

  const removeImageBanner = (e) => {
    setFileBanner();

    let newCampValues = camp;
    newCampValues.banner.image = null;
    newCampValues.banner.text = '';

    setCamp(newCampValues);
  }

  const uploadBackgroundImage = (e) => {
    const file = e.target.files[0];
    if (file.type.indexOf("image") < 0) {
      setFileBackground();
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onloadend = function (e) {
      setFileBackground(reader.result);
    };
  }

  const removeImageBackground = (e) => {
    setFileBackground();

    let newCampValues = camp;
    newCampValues.backgroundImage = null;

    setCamp(newCampValues);
  }

  ////////////////////////////////////
  const saveChanges = () => {
    axios
      .post(`${apiUrl}campaign/update/${id}`, {
        banner: {
          image: fileBanner || camp.banner.image || null,
          text: document.getElementById('link').value || camp.banner.text || null
        },
        avatar: file || camp.avatar || null,
        fontColor: camp.fontColor,
        headerColor: camp.headerColor,
        loadText:  document.getElementById('loadText').value || camp.loadText || null,
        welcomeText:  {
          text: document.getElementById('welcomeText').value || camp.welcomeText.text || 'Bienvenido',
          size: document.getElementById('font-size-welcome-text').value || camp.welcomeText.size || 18
        },
        urlOrigen:  document.getElementById('urlOrigen').value || camp.urlOrigen || null,
        name: camp.name,
        typeLogin: parseInt(camp.typeLogin) || 2,
        backgroundImage: fileBackground || camp.backgroundImage || null,
        backgroundColor: camp.backgroundColor || '#fff'
      })
      .then((res) => {
        setOpen(true);
        const result = res.data;
        return result;
      })
      .catch((error) => {
        console.log("error", error, camp.welcomeText);
      });

      
  };

  if (!camp)
    return (
      <div className={classes.circularLoading}>
        <CircularProgress size={60} />
      </div>
    );
  return (
    <Container>
      <Paper className={classes.campaniaInfo} elevation={3}>
        <div className={classes.headerDivCampaign}>
          <p> Info Campaa {camp.name} </p>
          <IconButton
            color="primary"
            aria-label="save"
            component="span"
            onClick={saveChanges}
          >
            <Save />
          </IconButton>
        </div>
        <TextField
          xs={12}
          sm={6}
          defaultValue="Disabled"
          disabled
          id="title-input"
          label="Name"
          name="name"
          margin="normal"
          variant="outlined"
          className={classes.textInputTitle}
          value={camp.name}
          onChange={e => handleChange(e, "name")}
        />
        <HtmlTooltip
          placement="right-start"
          title={
            <React.Fragment>
              <Typography color="inherit">Texto informativo</Typography>
              <p>
                {"Lorem ipsum dolor sit amet, consectetur adipiscing elitm"}
              </p>
            </React.Fragment>
          }
        >
          <Button>
            <HelpIcon />
          </Button>
        </HtmlTooltip>
        <TextField
          id="urlOrigen"
          label="Url de la campaa"
          name="url"
          margin="normal"
          variant="outlined"
          className={classes.textInputTitle}
          placeholder={camp.urlOrigen || 'Url de la campaa'}
        />
        <HtmlTooltip
          placement="right-start"
          title={
            <React.Fragment>
              <Typography color="inherit">Texto informativo</Typography>
              <p>
                {"Lorem ipsum dolor sit amet, consectetur adipiscing elitm"}
              </p>
            </React.Fragment>
          }
        >
          <Button>
            <HelpIcon />
          </Button>
        </HtmlTooltip>
        <FormControl className={classes.selectInput} variant="outlined">
          <InputLabel id="typeLogin-select">Login Type</InputLabel>
          <Select
            labelId="typeLogin-select"
            label="Login Type"
            id="typeLogin-select"
            value={camp.typeLogin ? camp.typeLogin : 3}
            onChange={e => handleChange(e, "typeLogin")}
          >
            <MenuItem key={2} value={"2"}>
              Just Name
            </MenuItem>
            <MenuItem key={3} value={"3"}>
              Just Email
            </MenuItem>
          </Select>
        </FormControl>
        <HtmlTooltip
          placement="right-start"
          title={
            <React.Fragment>
              <Typography color="inherit">Texto informativo</Typography>
              <p>
                {"Lorem ipsum dolor sit amet, consectetur adipiscing elitm"}
              </p>
            </React.Fragment>
          }
        >
          <Button>
            <HelpIcon />
          </Button>
        </HtmlTooltip>
        <Grid container spacing={3}>
          <Grid item xs={12} sm={6} className={classes.defaultGrid}>
            <div>
              <h2>
                Avatar
                <HtmlTooltip
                  placement="right-start"
                  title={
                    <React.Fragment>
                      <Typography color="inherit">Texto informativo</Typography>
                      <p>
                        {
                          "Lorem ipsum dolor sit amet, consectetur adipiscing elitm"
                        }
                      </p>
                    </React.Fragment>
                  }
                >
                  <Button>
                    <HelpIcon />
                  </Button>
                </HtmlTooltip>
                <HtmlTooltip
                  placement="right-end"
                >
                  <Button>
                    <Delete 
                      onClick={ e => removeImage(e) }
                    />
                  </Button>
                </HtmlTooltip>
              </h2>
              <Avatar className={classes.campAvatar}>
                <img
                  src={file ? file : camp.avatar}
                  width="100"
                  alt="Upload Preview"
                  className={classes.imgPreview}
                />
              </Avatar>

              <Input
                type="file"
                id="file"
                name="file"
                inputProps={{ accept: "image/*" }}
                placeholder="Upload an image."
                className={classes.fileInput}
                onChange={(e) => uploadImage(e)}
              />
            </div>
          </Grid>

          <Grid item xs={12} sm={6} className={classes.defaultGrid}>
            <div>
              <h2>
                Banner
                <HtmlTooltip
                  placement="right-start"
                  title={
                    <React.Fragment>
                      <Typography color="inherit">Texto informativo</Typography>
                      <p>
                        {
                          "Lorem ipsum dolor sit amet, consectetur adipiscing elitm"
                        }
                      </p>
                    </React.Fragment>
                  }
                >
                  <Button>
                    <HelpIcon />
                  </Button>
                </HtmlTooltip>
                <HtmlTooltip
                  placement="right-end"
                  title="Eliminar"
                >
                  <Button>
                    <Delete 
                      onClick={(e) => removeImageBanner(e) }
                    />
                  </Button>
                </HtmlTooltip>
              </h2>
              <div className={classes.campAvatar}>
                <img
                  src={
                    fileBanner
                      ? fileBanner
                      : camp.banner
                      ? camp.banner.image
                      : ""
                  }
                  width="100"
                  alt="Upload Preview"
                  className={classes.imgPreview}
                />
              </div>

              <Input
                type="file"
                id="file"
                name="file"
                inputProps={{ accept: "image/*" }}
                placeholder="Upload an image."
                className={classes.fileInput}
                onChange={(e) => uploadBannerImage(e)}
              />

              <Input
                type="text"
                id="link"
                name="link"
                placeholder={ camp.banner.text || "Link for image" }
              />
            </div>
          </Grid>
        
          <Grid item xs={12} sm={6} className={classes.defaultGrid}>
              <div>
                <h2>
                  Background image
                  <HtmlTooltip
                    placement="right-start"
                    title={
                      <React.Fragment>
                        <Typography color="inherit">Texto informativo</Typography>
                        <p>
                          {
                            "Lorem ipsum dolor sit amet, consectetur adipiscing elitm"
                          }
                        </p>
                      </React.Fragment>
                    }
                  >
                    <Button>
                      <HelpIcon />
                    </Button>
                  </HtmlTooltip>
                  <HtmlTooltip
                    placement="right-end"
                    title="Eliminar"
                  >
                    <Button>
                      <Delete 
                        onClick={(e) => removeImageBackground(e) }
                      />
                    </Button>
                  </HtmlTooltip>
                </h2>
                <Avatar className={classes.campAvatar}>
                  <img
                    src={ fileBackground
                        ? fileBackground
                        : camp.backgroundImage
                        ? camp.backgroundImage
                        : ""
                    }
                    width="100"
                    alt="No hay imagen cargada"
                    className={classes.imgPreview}
                  />
                </Avatar>

                  <Input
                    type="file"
                    id="file"
                    name="file"
                    inputProps={{ accept: "image/*" }}
                    placeholder="Upload an image."
                    className={classes.fileInput}
                    onChange={(e) => uploadBackgroundImage(e)}
                  />
              </div>
          </Grid>

          <Grid item xs={12} sm={6} className={classes.defaultGrid}>
            <Grid item xs={12} sm={6}>
                <h2>
                  Background Color
                </h2>
                <BlockPicker
                  color={camp.backgroundColor}
                  onChange={handleBackColor}
                />
              </Grid>
          </Grid>
        </Grid>
        <hr />
        <Grid container spacing={3}>
          <Grid item xs={12} sm={6} className={classes.defaultGrid}>
            <div>
              <h2>
                Header Color
                <HtmlTooltip
                  placement="right-start"
                  title={
                    <React.Fragment>
                      <Typography color="inherit">Texto informativo</Typography>
                      <p>
                        {
                          "Lorem ipsum dolor sit amet, consectetur adipiscing elitm"
                        }
                      </p>
                    </React.Fragment>
                  }
                >
                  <Button>
                    <HelpIcon />
                  </Button>
                </HtmlTooltip>
              </h2>
              <BlockPicker
                color={camp.headerColor}
                onChange={handleChangeColor}
              />
            </div>
          </Grid>
          <Grid item xs={12} sm={6} className={classes.defaultGrid}>
            <div>
              <h2>
                Font Color
                <HtmlTooltip
                  placement="right-start"
                  title={
                    <React.Fragment>
                      <Typography color="inherit">Texto informativo</Typography>
                      <p>
                        {
                          "Lorem ipsum dolor sit amet, consectetur adipiscing elitm"
                        }
                      </p>
                    </React.Fragment>
                  }
                >
                  <Button>
                    <HelpIcon />
                  </Button>
                </HtmlTooltip>
              </h2>
              <BlockPicker
                color={camp.fontColor}
                onChange={handleChangeColorFont}
              />
            </div>
          </Grid>
        </Grid>
        <Grid container spacing={3}>
          <Grid item xs={6} sm={6} className={classes.defaultGrid}>
            <div>
              <TextField
                id="loadText"
                label="Load Text"
                name="loadText"
                margin="normal"
                className={classes.textInputLoadText}
                placeholder={camp.loadText || 'Texto de espera en cliente'}
                variant="outlined"
              />
              <HtmlTooltip
                placement="right-start"
                title={
                  <React.Fragment>
                    <Typography color="inherit">Texto informativo</Typography>
                    <p>
                      {
                        "Lorem ipsum dolor sit amet, consectetur adipiscing elitm"
                      }
                    </p>
                  </React.Fragment>
                }
              >
                <Button>
                  <HelpIcon />
                </Button>
              </HtmlTooltip>
            </div>
          </Grid>
          <Grid item xs={6} sm={6} className={classes.columnGrid}>
            <div>
              <TextField
                id="welcomeText"
                label="Texto de bienvenida"
                name="welcomeText"
                margin="normal"
                className={classes.textInputLoadText}
                placeholder={camp.welcomeText.text || 'Texto de bienvenida'}
                variant="outlined"
              />
              <HtmlTooltip
                placement="right-start"
                title={
                  <React.Fragment>
                    <Typography color="inherit">Texto informativo</Typography>
                    <p>
                      {
                        "Lorem ipsum dolor sit amet, consectetur adipiscing elitm"
                      }
                    </p>
                  </React.Fragment>
                }
              >
                <Button>
                  <HelpIcon />
                </Button>
              </HtmlTooltip>
            </div>
            <div style={{ display: 'flex', justifyContent: 'center' }}>
              <Input
                type="number"
                inputProps={{ min: '0', max: '180' }}
                id="font-size-welcome-text"
                name="font-size-welcome-text"
                placeholder={ camp.welcomeText.size || 18 }
              />
              <span style={{ alignSelf: 'center' }}>{ 'px' }</span>
            </div>
          </Grid>
        </Grid>
      </Paper>

      {/*<Grid container spacing={3} className={classes.dataGrid}>
        <Grid item xs={12} sm={6}>
          <Paper elevation={3} className={classes.defaultPaper}>
            <div>
              <p className={classes.iconTitle}>Sellers in the Campaign</p>
              <Avatar className={classes.personIcon}>
                <PersonIcon />
              </Avatar>
              <h2 className={classes.iconNumber}>{camp ? camp.online : 0}</h2>
              <p className={classes.iconNumber}>{"0.00 % Online"}</p>
            </div>
          </Paper>
        </Grid>
        <Grid item xs={12} sm={6}>
          <Paper elevation={3} className={classes.defaultPaper}>
            <div>
              <p className={classes.iconTitle}>NPS</p>
              <Avatar className={classes.npcIcon}>
                <StarIcon />
              </Avatar>
              <h2 className={classes.iconNumber}>{camp ? camp.nps : 0}</h2>
            </div>
          </Paper>
        </Grid>
      </Grid>*/}

      <Paper className={classes.campaniaInfo} elevation={3}>
        <div className={classes.headerDiv}>
          <p>Sellers</p>
        </div>
        <Paper className={classes.searchVendedor}>
          <SearchSellers sellers={sellersCompany} addSeller={addSeller} />
        </Paper>

        <MaterialTable
          title="Sellers"
          style={{ marginBottom: "30px", width: "95%", marginLeft: "2%" }}
          columns={[
            {
              title: "Avatar",
              field: "avatar",
              editable: "never",
              render: (rowData) =>
                rowData.avatar ? (
                  <img
                    src={rowData.avatar}
                    alt="avatar"
                    style={{ width: 50, borderRadius: "30%" }}
                  />
                ) : (
                  <Avatar>
                    {rowData.name
                      .match(/\b(\w)/g)
                      .join("")
                      .toUpperCase()}
                  </Avatar>
                ),
              headerStyle: {
                width: "25%",
              },
            },
            {
              title: "Email",
              field: "mail",
              editable: "never",
              headerStyle: {
                width: "30%",
                textAlign: "center",
              },
            },
            {
              title: "Name",
              field: "name",
              editable: "never",
              headerStyle: {
                width: "30%",
                textAlign: "center",
              },
              cellStyle: {
                padding: "0px",
              },
            },
            {
              title: "NPS",
              field: "nps",
              editable: "never",
              headerStyle: {
                width: "30%",
              },
            },
          ]}
          data={sellers}
          editable={{
            onRowDelete: (oldData) =>
              new Promise((resolve, reject) => {
                let c;
                oldData.campaigns.forEach((campaign) => {
                  if (campaign.id === id)
                    c = { nps: campaign.nps, name: campaign.name };
                });
                if (c)
                  axios({
                    method: "delete",
                    url: `${apiUrl}campaign/assign/`,
                    headers: {
                      "Content-Type": "application/json",
                      Accept: "application/json",
                    },
                    data: {
                      campaignId: id,
                      userId: oldData.uid,
                      campaignNps: c.nps,
                      name: c.name,
                    },
                  })
                    .then((resp) => {
                      let newList = [];
                      allSellers.map(seller => {
                        console.log(seller);
                        if(seller.uid != oldData.uid) newList.push(seller);
                      });
                      getSellersCampaign(id, newList);
                      resolve();
                    })
                    .catch((error) => {
                      console.log("error adding seller", error);
                      //setSellers([]);
                      reject();
                    });
              }),
          }}
          actions={[
            {
              icon: ArrowForwardIosIcon,
              tooltip: "View Seller",
              onClick: (event, rowData) => goToSeller(rowData),
            },
          ]}
          options={{
            pageSize: 10,
            pageSizeOptions: [5, 10, 20],
          }}
        />
      </Paper>

      <Paper className={classes.campaniaInfo} elevation={3}>
        <div className={classes.headerDiv}>
          <p>Resources</p>
        </div>
        <MaterialTable
          title="Resources"
          style={{
            marginTop: "20px",
            marginBottom: "30px",
            width: "95%",
            marginLeft: "2%",
          }}
          columns={[
            {
              title: "Name",
              field: "name",
              editable: "always",
              headerStyle: {
                width: "25%",
                textAlign: "center",
              },
            },

            {
              title: "Type",
              field: "type",
              editable: "always",
              lookup: {
                0: "Indicencias",
                1: "Videos",
                2: "Texto",
                3: "Link Productos",
                4: "Link Soporte",
                5: "Imagen",
              },
              headerStyle: {
                width: "25%",
              },
            },
            {
              title: "Content",
              field: "content",
              editable: "always",
              headerStyle: {
                width: "25%",
              },
            },
          ]}
          data={resources}
          editable={{
            onRowAdd: (newData) =>
              axios
                .post(`${apiUrl}resource/`, {
                  idCampaign: id,
                  name: newData.name,
                  type: parseInt(newData.type) || 0,
                  recurso: newData.content,
                })
                .then((res) => {
                  getResoursesforCampaign();
                })
                .catch((error) => {
                  console.log("error", error);
                }),
            onRowDelete: (oldData) =>
              axios
                .delete(`${apiUrl}resource/`, {
                  data: {
                    campaignId: id,
                    resourceId: oldData.id,
                  },
                })
                .then((res) => {
                  console.log("res", res);
                  setResources(resources.filter((r) => r.id !== oldData.id));
                })
                .catch((error) => {
                  console.log("error", error);
                }),
            onRowUpdate: (oldData, newData) =>
              axios
                .post(`${apiUrl}resource/update`, {
                  idCampaign: id,
                  resourceId: oldData.id,
                  name: oldData.name,
                  recurso: oldData.content,
                  type: parseInt(oldData.type),
                })
                .then((res) => {
                  getResoursesforCampaign();
                }),
          }}
          options={{
            pageSize: 10,
            pageSizeOptions: [5, 10, 20],
          }}
        />
      </Paper>

      <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="success">
          Se guardaron los cambios
        </Alert>
      </Snackbar>
    </Container>
  );
};

export default Campaign;

